@font-face {
  font-family: "Satoshi";
  src: url("../fonts/satoshi/Satoshi-Variable.woff2") format("woff2"),
    url("../fonts/satoshi/Satoshi-Variable.woff") format("woff"),
    url("../fonts/satoshi/Satoshi-Variable.ttf") format("truetype");
  font-weight: 300 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Satoshi";
  src: url("../fonts/satoshi/Satoshi-VariableItalic.woff2") format("woff2"),
    url("../fonts/satoshi/Satoshi-VariableItalic.woff") format("woff"),
    url("../fonts/satoshi/Satoshi-VariableItalic.ttf") format("truetype");
  font-weight: 300 900;
  font-style: italic;
  font-display: swap;
}

:root {
  --font-satoshi: "Satoshi", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, sans-serif;
}
